import React, { useRef, useEffect, useState } from "react";
import styled from "styled-components";
import { useSvgResize } from "../../../hooks";
import { select } from "d3-selection";
import { colorGreen, colorGreyLight } from "../styles";
import { scaleLinear } from 'd3-scale'

const ChartWrapper = styled.div`
  position: relative;
  height: 80%;
  width: 90%;

  border: 1px solid ${colorGreyLight};

  border-radius: 20px;
  overflow: hidden;

  svg {
    position: absolute;
    width: 100%;
    height: 100%;

    rect {
      fill: ${colorGreen};
    }
  }
`;

const PercentageChart = ({ 
  data
}) => {
  const svgRef = useRef();
  const storedValues = useRef();
  const [init, setInit] = useState(false);
  let initVis

  const dims = useSvgResize(svgRef);

  useEffect(() => {
    if (dims.width && dims.height && data && !init) {
      initVis();
      setInit(true);
    }
  }, [data, dims.height, dims.width, init, initVis]);

  useEffect(() => {
    //console.log('dims', dims)
  }, [dims]);

  useEffect(() => {
    //console.log('data', data)
  }, [data]);

  initVis = () => {
    const svg = select(svgRef.current);
    svg
      .append("g")
      .attr("class", "chart-area")
    const chartArea = select(".chart-area");
    chartArea
      .append('rect')
      .attr('x', 0)
      .attr('y', 0)
      .attr('height', dims.height)
      .attr('width', dims.width/2)
    const xScale = scaleLinear().range([0, dims.width]).domain([0, 1])

    storedValues.current = {
      chartArea
    };
  };

  const updateData = () => {};

  const resetChart = () => {};

  const updateDims = () => {};

  const createUpdateRect = () => {
    const { chartArea } = storedValues.current
    
  }

  return (
    <ChartWrapper>
      <svg ref={svgRef} />
    </ChartWrapper>
  );
};

export default PercentageChart;
